    margin: 0;
    padding: 0;
  }

  .air-datepicker-body--day-name {
    color: var(--clr-text-secondary);
    font-size: 0.875rem;
    text-transform: none;
    width: 2.5rem;
    height: 2.5rem;
  }

  .air-datepicker-body--cells.-days- {
    grid-template-columns: repeat(7, 2.5rem);
    grid-auto-rows: 2.5rem;
  }

  .air-datepicker-cell {
    font-size: 0.875rem;

    &:hover {
      background: var(--clr-bg-secondary);
    }
  }

  .air-datepicker-cell.-day-.-other-month-,
  .air-datepicker-cell.-day-.-disabled- {
    color: var(--clr-text-tertiary);
  }

  .air-datepicker-cell.-selected- {
    background-color: var(--clr-text-light-blue);
    color: var(--clr-bg-primary);
    font-weight: 600;
  }

  .air-datepicker-cell.-current- {
    color: var(--clr-text-light-blue);

    &.-selected- {
      color: var(--clr-bg-primary);
      font-weight: 600;
    }
  }
}
/* Calendar end */

/* Pickers start */
.priority-picker,
.list-picker {
  display: none;
  opacity: 0;
  position: absolute;
  background-color: var(--clr-dark-blue-500);
  padding: 0.75rem 1rem;
  border: 1px solid var(--clr-divider);
  border-radius: 0.5rem;
  transition: opacity 0.2s ease-in-out;
  z-index: 10;

  &.visible {
    display: block !important;
    opacity: 1;
  }

  h5 {
    font-size: 1rem;
    font-weight: 500;
    margin: 0;
    margin-bottom: 0.625rem;
  }

  > ul {
    display: grid;
    gap: 0.25rem;
  }

  li {
    padding: 0.5rem;
    display: flex;
    gap: 0.25rem;
    cursor: pointer;

    .text {
      color: var(--clr-text-secondary);
      font-size: 0.9rem;
    }

    &.active {
      background-color: var(--clr-bg-secondary);
      border-radius: 0.25rem;
      .text {
        color: var(--clr-text-light-blue);
        font-weight: 500;
      }
    }
  }
}

.priority-picker {
  left: 0;
  top: 86%;

  li {
    .exclamation {
      font-size: 0.875rem;
      width: 1rem;
      height: 1rem;
      text-align: center;
      display: flex;
      &.low {
        color: var(--clr-text-light-blue);
      }
      &.medium {
        color: var(--clr-yellow-400);
      }
      &.high {
        color: var(--clr-red-400);
      }
    }
  }
}

.list-picker {
  display: none !important;
  left: 1rem;
  bottom: -10.25rem;

  li {
    align-items: center;
    gap: 0.5rem;
  }
}
/* Pickers end */

/* Desktops */
@media (min-width: 992px) {
  body {
    padding: 0.75rem;
    overflow-x: hidden;
  }

  body:has(.sidebar.visible) {
    pointer-events: auto;
  }

  #content {
    min-height: 97svh;
    display: grid;
    grid-template-columns: 22rem 1fr;
    gap: 6.5rem;
  }

  .btn-add {
    transition: background-color 0.3s ease;
    &::before {
      content: '';
      display: inline-block;
      width: 1.25rem;
      height: 1.25rem;
      background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
      background-repeat: no-repeat;
    }

    &:hover {
      background-color: hsla(217, 48%, 20%, 1);
    }
  }

  .btn-sidebar {
    display: none;
    pointer-events: none;
  }

  .sidebar {
    position: revert;
    transform: initial;
    min-width: 21.75rem;
    padding: 2rem;
    border-radius: 1.5rem;
    border: 1px solid var(--clr-divider, #1c2533);

    .greeting img {
      width: 2.5rem;
      height: 2.5rem;
    }
  }

  .main {
    max-width: 34rem;
    margin-top: 4rem;
    padding: 0;
  }

  dialog {
    padding: 2rem;
  }

  dialog#modal-add-task {
    padding: 1.25rem;
  }

  #modal-start {
    width: fit-content;
    .logo {
      width: 4.125rem;
    }

    h1 {
      min-width: 20rem;
    }

    > p {
      max-width: 20rem;
      margin-bottom: 2rem;
    }

    button {
      padding-block: 1rem;
    }

    .pagination {
      margin-top: 2rem;
    }

    input {
      margin-bottom: 2rem;
    }
  }

  dialog#modal-task {
    margin-top: 11.125rem;
    width: 36.5rem;
    max-width: initial;
    min-height: initial;

    .row-3 {
      display: flex;
      justify-content: space-between;
    }
  }

  .priority-picker,
  .list-picker {
    li {
      transition: background-color 0.2s ease;
      &:hover {
        background-color: var(--clr-bg-secondary);
        border-radius: 0.25rem;

        .text {
          color: var(--clr-text-primary);
        }
      }
    }
  }

  .priority-picker {
    left: initial;
    right: 0;
    top: 70%;
  }
}
