AgB;IAChB,gBAAgB;IAChB,UAAU;EACZ;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;IAClB;MACE,eAAe;IACjB;;IAEA;MACE,gBAAgB;IAClB;;IAEA;MACE,gBAAgB;MAChB,mBAAmB;IACrB;;IAEA;MACE,mBAAmB;IACrB;;IAEA;MACE,gBAAgB;IAClB;;IAEA;MACE,mBAAmB;IACrB;EACF;;EAEA;IACE,qBAAqB;IACrB,cAAc;IACd,kBAAkB;IAClB,mBAAmB;;IAEnB;MACE,aAAa;MACb,8BAA8B;IAChC;EACF;;EAEA;;IAEE;MACE,sCAAsC;MACtC;QACE,yCAAyC;QACzC,sBAAsB;;QAEtB;UACE,8BAA8B;QAChC;MACF;IACF;EACF;;EAEA;IACE,aAAa;IACb,QAAQ;IACR,QAAQ;EACV;AACF","sourcesContent":[":root {\n  /* Primitives */\n  --clr-dark-blue-300: hsla(218, 48%, 17%, 1);\n  --clr-dark-blue-400: hsla(218, 47%, 13%, 1);\n  --clr-dark-blue-500: hsla(218, 48%, 10%, 1);\n  --clr-dark-blue-700: hsla(215, 50%, 5%, 1);\n  --clr-light-blue-400: hsla(223, 87%, 76%, 1);\n  --clr-yellow-400: hsla(58, 99%, 72%, 1);\n  --clr-red-400: hsla(0, 68%, 60%, 1);\n  --clr-accent-brand: hsla(241, 61%, 55%, 1);\n\n  /* Background */\n  --clr-bg-primary: var(--clr-dark-blue-400);\n  --clr-bg-secondary: var(--clr-dark-blue-300);\n  --clr-bg-overlay: var(--clr-dark-blue-700);\n  --clr-bg-overlay-2: hsla(0, 0%, 0%, 0.5);\n  --clr-bg-sidebar: hsla(218, 48%, 10%, 1);\n  --clr-bg-btn-primary: var(--clr-accent-brand);\n\n  /* Text */\n  --clr-text-primary: hsla(0, 0%, 100%, 1);\n  --clr-text-secondary: hsla(0, 0%, 65%, 1);\n  --clr-text-tertiary: hsla(0, 0%, 41%, 1);\n  --clr-text-light-blue: var(--clr-light-blue-400);\n\n  /* Divider */\n  --clr-divider: hsla(217, 29%, 15%, 1);\n\n  /* List marker colors */\n  --clr-list-green: hsla(115, 63%, 77%, 1);\n  --clr-list-blue: hsla(209, 86%, 71%, 1);\n  --clr-list-pink: hsla(320, 94%, 73%, 1);\n  --clr-list-purple: hsla(265, 38%, 58%, 1);\n  --clr-list-orange: hsla(25, 89%, 60%, 1);\n  --clr-list-mint: hsla(173, 96%, 82%, 1);\n\n  /* Font sizes */\n  --fs-h1: clamp(1.5rem, 1.3269rem + 0.7692vw, 1.875rem);\n  --fs-body: clamp(0.875rem, 0.8173rem + 0.2564vw, 1rem);\n}\n\n/* Mini reset */\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  overflow-x: hidden;\n}\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\nhtml {\n  -moz-text-size-adjust: none;\n  -webkit-text-size-adjust: none;\n  text-size-adjust: none;\n}\n\nbody,\nh1,\nh2,\nh3,\nh4,\np,\nfigure,\nblockquote,\ndl,\ndd {\n  margin-block-end: 0;\n}\n\nul {\n  list-style: none;\n}\n\nbody {\n  min-height: 100vh;\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n  font-size-adjust: 0.52;\n}\n\nh1,\nh2,\nh3,\nh4,\nbutton,\ninput,\nlabel {\n  line-height: 1.1;\n}\n\nh1,\nh2,\nh3,\nh4 {\n  text-wrap: balance;\n}\n\np {\n  text-wrap: pretty;\n  margin: 0;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\nul {\n  padding: 0;\n  margin: 0;\n}\n\nimg,\npicture {\n  max-width: 100%;\n  display: block;\n}\n\nbutton {\n  border: none;\n  cursor: pointer;\n  background-color: transparent;\n  padding: 0;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font-family: inherit;\n  font-size: inherit;\n}\n\n:target {\n  scroll-margin-block: 5ex;\n}\n\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n/* Mini reset end */\n\n/* Custom fonts */\n@font-face {\n  font-family: 'Roobert PRO';\n  src: url('./assets/fonts/RoobertPRO-Regular.woff2') format('woff2');\n  font-weight: 400;\n  font-style: normal;\n  font-display: block;\n}\n\n@font-face {\n  font-family: 'Roobert PRO';\n  src: url('./assets/fonts/RoobertPRO-Medium.woff2') format('woff2');\n  font-weight: 500;\n  font-style: normal;\n  font-display: block;\n}\n\n@font-face {\n  font-family: 'Roobert PRO';\n  src: url('./assets/fonts/RoobertPRO-Bold.woff2') format('woff2');\n  font-weight: 700;\n  font-style: normal;\n  font-display: block;\n}\n\n/* Styles */\nbody {\n  font-family: 'Roobert PRO', sans-serif;\n  background-color: var(--clr-bg-primary);\n  text-rendering: optimizeSpeed;\n  line-height: 1.5;\n}\n\nbody:has(.sidebar.visible) {\n  pointer-events: none;\n}\n\n#content {\n  min-height: 100svh;\n}\n\n#content:has(dialog) {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 2rem;\n}\n\nh1 {\n  font-size: var(--fs-h1);\n}\n\np,\nbutton {\n  font-size: var(--fs-body);\n}\n\nh1,\nh2,\nh3,\nh4 {\n  margin: 0;\n  padding: 0;\n  color: var(--clr-text-primary);\n}\n\nh3 {\n  font-size: 1.25rem;\n  font-weight: 700;\n}\n\n#list-title {\n  color: var(--clr-text-light-blue);\n  font-size: clamp(1.875rem, 1.4712rem + 1.7949vw, 2.75rem);\n  font-weight: 700;\n}\n\n#todays-date {\n  font-size: clamp(1rem, 0.9423rem + 0.2564vw, 1.125rem);\n  color: var(--clr-text-secondary);\n  margin-top: 0.5rem;\n}\n\n.sidebar {\n  width: 18.375rem;\n  height: 100%;\n  position: fixed;\n  top: 0px;\n  left: 0;\n  z-index: 100;\n  transform: translateX(-60rem);\n  transition: transform 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);\n\n  padding: 2rem 1.5rem;\n  display: flex;\n  flex-direction: column;\n  gap: 2rem;\n  background-color: var(--clr-bg-sidebar);\n  border-radius: 0 1rem 1rem 0;\n  border-top: 1px solid var(--clr-divider, #1c2533);\n  border-right: 1px solid var(--clr-divider, #1c2533);\n  border-bottom: 1px solid var(--clr-divider, #1c2533);\n\n  &.visible {\n    transform: translateX(0);\n  }\n\n  .greeting {\n    display: flex;\n    align-items: center;\n    gap: 0.75rem;\n\n    img {\n      width: 2rem;\n      height: 2rem;\n    }\n  }\n\n  ul {\n    display: flex;\n    flex-direction: column;\n\n    > li button {\n      padding: 0.75rem;\n      width: 100%;\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      height: 3rem;\n      color: var(--clr-text-secondary);\n      border-radius: 0.625rem;\n\n      > div {\n        display: flex;\n        align-items: center;\n        gap: 0.75rem;\n\n        svg {\n          fill: var(--clr-text-secondary);\n        }\n\n        p {\n          font-size: 1rem;\n        }\n      }\n\n      span {\n        font-size: 0.875rem;\n      }\n\n      &.active {\n        background-color: var(--clr-bg-secondary);\n        color: var(--clr-text-light-blue);\n\n        p {\n          font-weight: 500;\n        }\n\n        svg {\n          fill: var(--clr-text-light-blue);\n        }\n      }\n    }\n  }\n\n  .custom-list {\n    h3 {\n      margin-bottom: 0.75rem;\n    }\n\n    svg {\n      fill: none;\n    }\n\n    button.active svg {\n      fill: none;\n    }\n\n    > ul li:first-child button > div svg {\n      stroke: var(--clr-list-green);\n    }\n\n    > ul li:nth-child(2) button > div svg {\n      stroke: var(--clr-list-blue);\n    }\n\n    > ul li:nth-child(3) button > div svg {\n      stroke: var(--clr-list-pink);\n    }\n\n    .btn-add-list {\n      display: flex;\n      align-items: center;\n      gap: 0.75rem;\n      margin-top: 0.5rem;\n      padding-block: 0.75rem;\n      width: 100%;\n      opacity: 1;\n      transition: all 0.4s ease-out;\n\n      span {\n        color: var(--clr-text-primary);\n        transition: color 0.4s ease;\n      }\n\n      &:hover span {\n        color: var(--clr-text-secondary);\n      }\n\n      &.hidden {\n        opacity: 0;\n        /* transform: translateY(-5rem);\n        position: absolute; */\n        display: none;\n      }\n    }\n\n    #form-custom-list {\n      margin-top: 0.5rem;\n      padding-block: 0.75rem;\n      display: flex;\n      align-items: end;\n      justify-content: space-between;\n      gap: 0.75rem;\n\n      > div {\n        width: 100%;\n        display: flex;\n        flex-direction: column;\n        gap: 0.5rem;\n\n        label {\n          font-size: 0.825rem;\n          color: var(--clr-text-secondary);\n        }\n\n        input {\n          border-radius: 0.325rem;\n          padding: 0.6rem;\n          width: 100%;\n          background-color: var(--clr-bg-secondary);\n          color: var(--clr-text-primary);\n          border: none;\n        }\n      }\n\n      button {\n        padding: 0.6rem 0.75rem;\n        border-radius: 0.325rem;\n        background-color: var(--clr-accent-brand);\n        color: var(--clr-text-primary);\n      }\n    }\n  }\n}\n\n.sidebar.visible,\n.sidebar.visible *,\n.sidebar.visible button,\n.sidebar.visible a {\n  pointer-events: auto !important;\n}\n\n.main {\n  margin-top: 1.5rem;\n  padding: 0 1rem;\n}\n\ndialog {\n  padding: 1.25rem;\n  background-color: var(--clr-bg-primary);\n  border: 1px solid var(--clr-divider);\n  border-radius: 1.5rem;\n\n  > * {\n    color: var(--clr-text-primary);\n  }\n\n  &::backdrop {\n    background-color: var(--clr-bg-overlay);\n  }\n}\n\ndialog#modal-task {\n  margin-inline: auto;\n  margin-top: 7rem;\n  width: 90%;\n  max-width: 28rem;\n  min-height: 14.25rem;\n  border-radius: 0.75rem;\n  position: relative;\n  padding: 1rem;\n  position: absolute;\n  background-color: var(--clr-bg-primary);\n  overflow: visible;\n\n  &::backdrop {\n    background-color: var(--clr-bg-overlay-2);\n  }\n\n  #btn-close-modal {\n    position: absolute;\n    width: 1.25rem;\n    height: 1.25rem;\n    top: 1rem;\n    right: 1rem;\n    cursor: pointer;\n  }\n\n  .row-3,\n  .row-4 {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n\n    > div {\n      display: flex;\n      align-items: center;\n      width: fit-content;\n      gap: 0.5rem;\n      cursor: pointer;\n\n      > * {\n        cursor: pointer;\n      }\n\n      > input {\n        max-width: 5.5rem;\n      }\n    }\n\n    .priority-picker {\n      display: none;\n      cursor: initial;\n\n      > * {\n        cursor: initial;\n      }\n    }\n  }\n\n  .row-3 {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 1.5rem;\n    position: relative;\n    padding-bottom: 1.25rem;\n    border-bottom: 1px solid var(--clr-divider);\n    margin-top: 1.5rem;\n  }\n\n  .row-4 {\n    margin-top: 1rem;\n\n    .actions {\n      display: flex;\n      align-items: center;\n      gap: 0.75rem;\n\n      #btn-save-changes,\n      #btn